@import './mixins.scss';

.hide {
    display: none
}

.cartify_ajax_product_widget {
    &:not(.expanded) {

        .agni-product-attributes,
        .agni-product-categories,
        .agni-product-brands,
        .agni-product-price,
        .agni-product-active-filters,
        .agni-product-ratings {
            display: none;
        }
    }
}


