.agni-product-attributes,
.agni-product-categories,
.agni-product-categories-taxonomy {

    .list-toggle {

        span {
            display: block;
        }

        span:last-child {
            display: none
        }
    }

    &.more .list-toggle {
        span {
            display: block;
        }

        span:first-child {
            display: none
        }
    }
}

.agni-product-price {
    &-slider {
        position: relative;
        // max-width: 200px;
        width: calc(100% - 13px);
        margin-left: 6px;
        margin-top: 7px;
        padding-top: 15px;

        &__handle {
            position: relative;
            width: 100%;
            height: 1px;
            display: block;
            margin-bottom: 8px;
        }



        &__handle {
            &--base {
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: $background_color_handle_default;
            }

            &--min,
            &--max {
                display: block;
                position: absolute;
                height: 100%;
                user-select: none;

                &:after {
                    content: '';
                    position: absolute;
                    right: 0;
                    width: 13px;
                    height: 13px;
                    margin-top: -6px;
                    margin-right: -7px;
                    border-radius: 50%;
                    background-color: #fff;
                    border: 2px solid;
                    border-color: $background_color_handle_highlight;
                    transition: all 0.2s ease;

                }

                &:hover {
                    &:after {
                        width: 17px;
                        height: 17px;
                        margin-top: -8px;
                        margin-right: -9px;
                    }
                }


            }

            &--min {
                background-color: $background_color_handle_default;
                z-index: 3;
                // width: 20%;
                width: 0%;
            }

            &--max {
                background-color: $background_color_handle_highlight;
                z-index: 2;
                // width: 70%;
                width: 100%;
            }

        }

        &__range {
            font-size: var(--cartify_font_size_small_1);
            display: flex;

            &--min {
                &:after {
                    content: "-";
                    margin: 0 3px;
                }
            }

        }
    }

    .agni-product-price-form {
        display: flex;

        &__min,
        &__max {
            position: relative;
            flex: 1;
            padding: 0 12px;

            &--input {
                display: flex;
                border: 1px solid #ddd;
                align-items: center;
                font-size: var(--cartify_font_size_small_2);

                span {
                    padding: 0px 0px 0px 6px;
                    color: #ccc;

                }
            }
        }

        &__min {
            padding-left: 0;

            &:after {
                content: "-";
                margin: 0 10px;
                position: absolute;
                bottom: 0;
                right: 0;
                line-height: 38px;
                margin-right: -3px;
            }
        }

        label {
            font-size: var(--cartify_font_size_small_1);
        }

        input {
            border: 0;
            padding: 10px 5px;

            /* Chrome, Safari, Edge, Opera */
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }

            /* Firefox */
            &[type=number] {
                -moz-appearance: textfield;
            }
        }

        button {
            border: 0px;
            padding: 0;
            text-decoration: underline;
            margin-top: 23px;

            &:before {
                display: none;
            }
        }
    }
}

@include devices_content(md){
    .agni-product-price-slider{
        max-width: 200px;
    }
