// Responsive 

$device_width_2xl: 1560;
$device_width_xl: 1440;
$device_width_lg: 1200;
$device_width_md: 1024;
$device_width_sm: 667;

$device_width:(sm: #{$device_width_sm}px,
    md: #{$device_width_md}px,
    lg: #{$device_width_lg}px,
    xl: #{$device_width_xl}px);
// 834
$device_width_wide:(sm: #{$device_width_sm}px,
    md: #{$device_width_md}px,
    lg: #{$device_width_lg}px,
    xl: #{$device_width_xl}px,
    2xl: #{$device_width_2xl}px);


$background_color_handle_default: #ddd;
$background_color_handle_highlight: #222;